.about-banner { 
    display:grid;    
    width: 100%;
    height: 650px;

   img {    
   width: 100%;
   position: absolute;
   filter: brightness(50%);
   top: 0;
   object-fit: cover;
   }

   .about-about {
       position:absolute;
       Z-index: 9999;
       top: 45%;
       left: 38%;
       color: white;
       font-size: 6em;
       text-transform: uppercase;
       
   }

   
}

.about-content {
    
    width: 65%;
    margin: 0 auto;
   
}